<template>
    <div id="list">
        <addSnippet @newSnippet="addSnippet"></addSnippet>
        <button @click="fetchLatest">Get latest snippets</button>
        <button @click="fetchBest">Get best snippets</button>
        <div v-show="!loading">{{shownList}}</div>
        <div v-show="loading">{{errormsg}}</div>
        <div class="snippets">
            <div class="snippet" v-for="snippet in snippets" :key="snippet.id">
                <div>Title:{{snippet.title}}</div>
                <div>Content:{{snippet.content}}</div>
                <div>Score: {{snippet.score}}</div>
                <editSnippet v-bind:identifier="snippet.id"></editSnippet>
            </div>
        </div>
    </div>
</template>

<script>
import addSnippet from './addSnippet.vue';
import editSnippet from './editSnippet.vue';


    export default {
        name: 'list',
        components: {
            addSnippet,
            editSnippet,
        },
        data: () => ({
            loading: Boolean,
            errormsg: String,
            shownList: 'Latest snippets are shown below',
            snippets:[]
        }),
        methods:{
            fetchLatest(){
                this.loading=true;
                this.errormsg = 'The API i loading please wait'
                fetch('https://www.forverkliga.se/JavaScript/api/api-snippets.php?latest', {
                    method: 'GET',
                })
                .then(response => response.json())
                .then((data) => {
                    this.shownList = 'Latest snippets are shown below';
                    this.loading=false;
                    this.snippets = data;
                })
                .catch((error) => {
                    this.errormsg = 'The API failed, please try again.';
                    console.log(error);
                })
            },
            fetchBest(){
                this.loading=true;
                this.errormsg = 'The API i loading please wait'
                fetch('https://www.forverkliga.se/JavaScript/api/api-snippets.php?best', {
                    method: 'GET',
                })
                .then(response => response.json())
                .then((data) => {
                    this.shownList = 'Best snippets are shown below';
                    this.loading=false;
                    this.snippets = data;
                })
                .catch((error) => {
                    this.errormsg = 'The API failed, please try again.';
                    console.log(error);
                })
            },
            addSnippet(snippet){
                console.log(snippet);
                this.snippets.push(snippet);
            }
        },
        mounted(){
            this.fetchLatest();
        }
    }
</script>

<style scopes>
    #list {

    }
    .snippets{
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-gap: 10em;
    }
    .snippet{
        border: 1px solid #cccc;
        padding:0.5em;
        
    }
</style>
